import axios from "axios";

class ModelsAgainstBrandAdapter {
  async adaptData(attachAttrib, apiData, modelsUrl, brand, modelSide) {
    let data;
    console.log("ModelsAgainstBrandAdapter: ",arguments);
    if (!modelsUrl || !attachAttrib.product || !brand || !modelSide) {
      return attachAttrib;
    }
    const url = modelsUrl.replace("<product>", attachAttrib.product).replace("<brand>", brand);

    let items = [];

    try {
      const response = await axios.get(url);
      data = response?.data?.data;
      // console.log("Models: ", data);
      for (let i = 0; data && i < data.length; i++) {
        if (data[i]) {
          try {
            const obj = {
              id: data[i].id,
              name: data[i].name,
              image: data[i].photo,
              product: attachAttrib.product,
            };
            items.push(obj);
          } catch (err) {
            console.log("ModelsAgainstBrandAdapter-data reading: ", err);
          }
        }
      }
    } catch (err) {
      console.log("ModelsAgainstBrandAdapter: ", err);
    }

    let ret = {
      ...attachAttrib,
      [modelSide]: items,
    };
    return ret;
  }
}

export default ModelsAgainstBrandAdapter;

